let storage = {
  apiKey: 'f2e9895481abedb65290e30543384cfb',
  sessionId: localStorage.getItem('session_id') || null,
  userId: localStorage.getItem('user_id') || null,
  language: 'ja',
  pageTitlePostfix: '  ' + document.title,
  listTypes: [
    {
      title: '',
      shortTitle: '',
      query: 'popular',
      type: 'collection',
      isCategory: true
    },
    {
      title: '',
      shortTitle: '',
      query: 'top_rated',
      type: 'collection',
      isCategory: true
    },
    {
      title: '',
      shortTitle: '',
      query: 'upcoming',
      type: 'collection',
      isCategory: true
    },
    {
      title: '',
      shortTitle: '',
      query: 'now_playing',
      type: 'collection',
      isCategory: true
    },
    {
      title: '',
      query: 'search',
      isCategory: false
    },
    {
      title: '',
      query: 'favorite',
      isCategory: false
    }
  ],
  categories: {},
  // For Browser History
  backTitle: '',
  moviePath: '',
  createMoviePopup: false,
  moviePopupOnHistory: false
};

// Create categories titles
storage.listTypes.forEach(function(listType){
  storage.categories[listType.query] = listType.title;
});

export default storage;



